h1, h2, h3, h4, h5, h6 {
  margin: 20px 0 10px;
  font-family: Roboto, Arial;
  font-weight: 500; }

h1,
.title-xlg {
  font-family: "Open Sans", Roboto, Arial;
  font-size: 22px; }

h2,
.title-lg {
  font-size: 18px; }

h3,
.title-md {
  font-size: 16px; }

h4,
.title-sm {
  font-size: 15px; }

h5,
.title-xs {
  font-size: 14px; }

p {
  margin: 10px 0; }

a {
  color: #2e99db;
  &.shy {
    color: inherit; } }

.small {
  font-size: 70%; }

.big {
  font-size: 120%; }

.xl {
  font-size: 150%; }

.muted {
  opacity: 0.5; }

.text-success {
  color: #66BB6A; }

.text-primary {
  color: #2e99db; }

.text-danger {
  color: #FF6431; }

.bg-silver {
  background: #eeeeee; }

.bg-success {
  background: #66BB6A !important; }

.bg-primary {
  background: #2e99db !important; }

.bg-danger {
  background: #FF6431 !important; }

.pull-right {
  float: right !important; }

.pull-left {
  float: left !important; }

.text-uppercase {
  text-transform: uppercase; }

.text-left {
  text-align: left !important; }

.text-center {
  text-align: center !important; }

.text-right {
  text-align: right !important; }

.bg-white {
  background: #fff; }

.labeled-info {
  font-size: 16px;
  &__label,
  label {
    display: block;
    color: #aaa;
    font-size: 12px; } }
