.radio {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  user-select: none;

  @include active {
    .icon {
      background-color: #eeeeee;
      &:before {
        transform: scale(0); } } }

  &__input,
  input {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    &:checked + .icon {
      &:before {
        background: #2e99db; } } }

  .icon {
    display: inline-block;
    vertical-align: middle;
    padding: 0;
    margin-right: 6px;
    border: solid #eeeeee 2px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    background: #fff;
    line-height: 30px;
    &:before {
      content: "";
      display: block;
      margin: 3px;
      border-radius: 10px;
      width: 20px;
      height: 20px;
      transform: scale(1);
      transition: transform 300ms ease-out; } } }

.radio-success {
  input {
    &:checked + .icon {
      &:before {
        background: #66BB6A; } } } }
