.main,
.container,
.sidebar,
.inner,
.application {
  position: relative;
  height: 100%; }

.main {
  overflow: auto; }
.inner {
  min-width: 980px;
  @include clr; }

.content {
  position: relative;
  z-index: 5; }

.container {
  position: relative;
  &_right {
    float: right;
    width: 60%; }
  &_overlay {
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;
    bottom: 0;
    width: 60%;
    box-shadow: -3px 0 4px 5px rgba(0, 0, 0, 0.2);
    transform: translate3d(-110%,0,0);
    transition: transform 0.3s ease-in-out;
    &.is-open {
      @include gpu; } }
  &__body {
    height: calc(100% - 50px);
    // position: absolute
    // top: 50px
    // left: 0
    // right: 0
 } }    // bottom: 0

.sidebar {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  background: $alabaster;
  &_left {
    // z-index: 1
    float: left;
    width: 40%;
    box-shadow: -3px 0 4px 5px rgba(0, 0, 0, 0.2); }
  &__body {
    position: relative;
    overflow: auto;
    flex: 1; }
  &__toolbar {
    .toolbar {
      &__col {
        &:not(:last-child) {
          border-color: #89cb8c; }
        &:first-child {
          @include refund {
            width: 0;
            opacity: 0;
            visibility: hidden;
            pointer-events: none; } } }
      &__button {
        @include refund {
          background: $red; } } } }
  //state
  &.is-disabled {
    .sidebar {
      &__toolbar,
      &__menu {
        display: none; } }
    .header__delete {
      display: none; } } }

.items {
  &__top {
    height: 180px; }
  &__body {
    height: calc(100% - 180px);
    // position: absolute
    // top: 180px
    // left: 0
    // right: 0
 } }    // bottom: 0


.wrapper {
  margin: 10px 0; }

.section {
  padding: 20px 0;
  &_inner {
    padding: 20px; } }
/////////////////
section {
  padding: 20px;
  .clear-hmargins {
    margin-left: -20px;
    margin-right: -20px; } }

.form-narrow {
  margin-left: auto;
  margin-right: auto;
  max-width: 460px; }

